/**
 *
 * Viewer - Image
 *
 * @author Takuto Yanagida
 * @version 2021-12-26
 *
 */


window['NACSS']           = window['NACSS']           || {};
window['NACSS']['viewer'] = window['NACSS']['viewer'] || {};


((NS) => {

	/**
	 *
	 * Functions for Hash
	 *
	 * @author Takuto Yanagida
	 * @version 2022-08-17
	 *
	 */
	
	
	function calcHash(str, asHex = true) {
		let h = 0x811c9dc5;
		for (let i = 0; i < str.length; i += 1) {
			h ^= str.charCodeAt(i);
			h += (h << 1) + (h << 4) + (h << 7) + (h << 8) + (h << 24);
		}
		if (asHex) {
			const hex = '0000000' + (h >>> 0).toString(16);
			return hex.substring(hex.length - 8);
		}
		return h >>> 0;
	}
	
	const cache = new Map();
	
	function calcHashSet(str, asHex = true) {
		if (cache.has(str)) {
			const n = cache.get(str);
			cache.set(str, n + 1);
			return calcHash(str + n, asHex);
		} else {
			cache.set(str, 1);
			return calcHash(str, asHex);
		}
	}
	
	/**
	 *
	 * Style Class Utilities
	 *
	 * @author Takuto Yanagida
	 * @version 2021-11-11
	 *
	 */
	
	
	function hasClass(tar, cls) {
		const key = cls.substr(1);
		if (cls.startsWith(':')) {
			return tar.dataset[key] !== undefined;
		} else {
			return tar.classList.contains(key);
		}
	}
	
	function setClass(tar, cls, enabled = true, val = '') {
		const key = cls.substr(1);
		if (cls.startsWith(':')) {
			if (enabled) {
				tar.dataset[key] = val;
			} else {
				delete tar.dataset[key];
			}
		} else {
			const c = [key, val].join('-');
			if (enabled) {
				tar.classList.add(c);
			} else {
				tar.classList.remove(c);
			}
		}
	}
	
	function getSelector(cls) {
		if (cls.startsWith(':')) {
			return `*[data-${cls.substr(1).replace(/([A-Z])/g, c => '-' + c.charAt(0).toLowerCase())}]`;
		} else {
			return `*${cls}`;
		}
	}
	
	/**
	 *
	 * Functions for Touch Support
	 *
	 * @author Takuto Yanagida
	 * @version 2021-10-18
	 *
	 */
	
	
	function getTouchPoint(ts) {
		const pxo = window.pageXOffset, pyo = window.pageYOffset;
		let x = 0, y = 0;
		if (ts.length === 1) {
			x = ts[0].pageX - pxo;
			y = ts[0].pageY - pyo;
		} else if (2 <= ts.length) {
			x = (ts[0].pageX + ts[1].pageX) / 2 - pxo;
			y = (ts[0].pageY + ts[1].pageY) / 2 - pyo
		}
		return [x, y];
	}
	
	function touchDistance(ts) {
		const x1 = ts[0].screenX, y1 = ts[0].screenY;
		const x2 = ts[1].screenX, y2 = ts[1].screenY;
		return Math.sqrt(Math.pow(x2 - x1, 2) + Math.pow(y2 - y1, 2));
	}
	
	function preventWindowTouchMove(f) {
		let isTouching = false;
		window.addEventListener('touchmove', (e) => {
			if (!isTouching) return;
			e.preventDefault();
			e.stopImmediatePropagation();
		}, { passive: false });
		f.addEventListener('touchstart', () => { isTouching = true; });
		f.addEventListener('touchend', () => { isTouching = false; });
	}
	
	function getCursorPoint(e) {
		return [e.pageX - window.pageXOffset, e.pageY - window.pageYOffset];
	}
	
	/**
	 *
	 * Utilities
	 *
	 * @author Takuto Yanagida
	 * @version 2021-12-06
	 *
	 */
	
	
	function getScrollOffset() {
		const s   = getComputedStyle(document.documentElement);
		const val = parseInt(s.scrollPaddingTop);
		return Number.isNaN(val) ? 0 : val;
	}
	
	
	// -----------------------------------------------------------------------------
	
	
	function throttle(fn) {
		let isRunning;
		return (...args) => {
			if (isRunning) return;
			isRunning = true;
			requestAnimationFrame(() => {
				isRunning = false;
				fn(...args);
			});
		};
	}
	
	const resizeListeners = [];
	
	function onResize(fn, doFirst = false) {
		if (doFirst) fn();
		resizeListeners.push(throttle(fn));
	}
	
	document.addEventListener('DOMContentLoaded', () => {
		window.addEventListener('resize', () => {
			for (const l of resizeListeners) l();
		}, { passive: true });
	});
	

	/**
	 *
	 * Image Viewer
	 *
	 * @author Takuto Yanagida
	 * @version 2022-08-17
	 *
	 */
	
	
	function apply(as, opts = {}) {
		if (as.length === 0) return;
	
		opts = Object.assign({
			styleOpener       : ':ncViewerOpener',
			styleOpenerWrapper: ':ncViewerOpenerWrapper',
		}, opts);
	
		const fs = as.map(a => {
			const id = calcHashSet(a.href);
			const [frameInst, frame] = createImageFrame(opts, a.href);
			const caption = extractImageCaption(a);
			assignImageOpenerStyle(a, opts['styleOpener'], opts['styleOpenerWrapper']);
	
			return { opener: a, id, frameInst, frame, caption };
		});
		initialize(fs, opts);
	}
	
	
	// -----------------------------------------------------------------------------
	
	
	function createImageFrame(opts, src) {
		opts = Object.assign({
			styleImageFrame: ':ncFrameImage',
			styleLoaded    : ':ncLoaded',
			maxZoomRate    : 10,
		}, opts);
	
		const inst = {
			opts, src,
	
			scale    : 1,
			realScale: 1,
			baseSize : null,
			isLs     : false,
			margin   : 0,
	
			frm: null,
			img: null,
		};
	
		inst.frm = document.createElement('div');
		setClass(inst.frm, inst.opts.styleImageFrame);
	
		inst.img = document.createElement('img');
		inst.img.addEventListener('click', e => e.stopPropagation());
		inst.frm.appendChild(inst.img);
	
		_enableMouseGesture(inst, inst.frm);
		_enableTouchGesture(inst, inst.frm);
	
		inst['doResize'] = () => setSize(inst);
		inst['doOpen']   = () => setSource(inst);
		inst['doClose']  = () => {};
		return [inst, inst.frm];
	}
	
	function extractImageCaption(a) {
		if (a.parentNode.tagName === 'FIGURE') {
			const fcs = a.parentNode.getElementsByTagName('figcaption');
			if (0 < fcs.length) {
				return fcs[0].innerHTML;
			}
		}
		return null;
	}
	
	function assignImageOpenerStyle(a, styleOpener, styleOpenerWrapper) {
		setClass(a, styleOpener);
		if (a.parentNode.tagName === 'FIGURE') {
			setClass(a.parentNode, styleOpenerWrapper);
		}
	}
	
	
	// -----------------------------------------------------------------------------
	
	
	function setSize(inst) {
		const winAs = inst.frm.offsetWidth / inst.frm.offsetHeight;
		const imgAs = inst.img.offsetWidth / inst.img.offsetHeight;
		inst.isLs = (winAs < imgAs);
	
		const cs = getComputedStyle(inst.img);
		inst.margin = parseInt(cs.outlineWidth) * 2;
	
		const s = inst.img.style;
		s.minWidth  = '';
		s.minHeight = '';
		if (inst.isLs) {
			s.width  = `calc(100% - ${inst.margin}px)`;
			s.height = 'auto';
		} else {
			s.width    = 'auto';
			s.height   = `calc(100% - ${inst.margin}px)`;
			s.maxWidth = 'none';
		}
	
		inst.baseSize = inst.isLs ? inst.frm.clientWidth  : inst.frm.clientHeight;
		const nbs     = inst.isLs ? inst.img.naturalWidth : inst.img.naturalHeight;
	
		inst.realScale = nbs / inst.baseSize;
		inst.scale     = 1;
		_doCenteringImage(inst.frm, inst.img);
	}
	
	function setSource(inst) {
		const img = inst.img;
		if (!img.src) {
			img.style.opacity = '0';
			img.src = inst.src;
			img.addEventListener('load', () => {
				setSize(inst);
				setClass(inst.frm, inst.opts.styleLoaded);
				setTimeout(() => { img.style.opacity = '1'; }, 0);
			});
		}
	}
	
	
	// -----------------------------------------------------------------------------
	
	
	function _setScaledSize(inst, scale) {
		inst.scale = Math.max(1, Math.min(inst.opts.maxZoomRate, scale));
	
		const size = (inst.baseSize * inst.scale - inst.margin) + 'px';
		if (inst.isLs) {
			inst.img.style.minWidth = size;
		} else {
			inst.img.style.minHeight = size;
		}
		_doCenteringImage(inst.frm, inst.img);
	}
	
	function _doCenteringImage(frm, img) {
		const imgW = img.offsetWidth, imgH = img.offsetHeight;
		const frmW = frm.clientWidth, frmH = frm.clientHeight;
		const s = img.style;
		s.left = (imgW < frmW) ? (((frmW - imgW) / 2) + 'px') : 0;
		s.top  = (imgH < frmH) ? (((frmH - imgH) / 2) + 'px') : 0;
	}
	
	function _enableMouseGesture(inst, frm) {
		let xS = 0, yS = 0;
		let isMoving = false;
	
		frm.addEventListener('mousedown', e => {
			if (e.button) return;  // when button is not left
			e.preventDefault();
			[xS, yS] = getCursorPoint(e);
			isMoving = true;
		});
		frm.addEventListener('mousemove', e => {
			if (!isMoving) return;
			e.stopPropagation();
			e.preventDefault();
	
			const [cx, cy] = getCursorPoint(e);
			frm.scrollLeft += xS - cx;
			frm.scrollTop  += yS - cy;
			xS = cx;
			yS = cy;
		});
		frm.addEventListener('mousedrag', e => {
			if (!isMoving) return;
			e.stopPropagation();
			e.preventDefault();
		});
		frm.addEventListener('mouseup', () => { isMoving = false; });
	
		frm.addEventListener('wheel', e => {
			const s = 0 > e.deltaY ? 1.1 : 0.9;
			zoomListener(e, inst.scale * s);
		}, true);
		frm.addEventListener('dblclick', e => {
			if (inst.realScale < 1) return;
			zoomListener(e, inst.scale < inst.realScale ? inst.realScale : 1);
		}, true);
	
		function zoomListener(e, scale) {
			e.stopPropagation();
			e.preventDefault();
	
			const [cx, cy] = getCursorPoint(e);
			const ix = (cx + frm.scrollLeft) / inst.scale;
			const iy = (cy + frm.scrollTop)  / inst.scale;
	
			_setScaledSize(inst, scale);
	
			frm.scrollLeft = ix * inst.scale - cx;
			frm.scrollTop  = iy * inst.scale - cy;
		}
	}
	
	function _enableTouchGesture(inst, frm) {
		let ix = 0, iy = 0;
		let lastTouchCount = 0;
		let baseDist = 0;
	
		preventWindowTouchMove(frm);  // for Android
	
		frm.addEventListener('touchstart', e => {
			baseDist = 0;
			updatePoint(e.touches);
		});
		frm.addEventListener('touchmove', e => {
			e.preventDefault();
			e.stopPropagation();
	
			const ts = e.touches;
			if (lastTouchCount !== ts.length) {
				updatePoint(ts);
			}
			const [cx, cy] = getTouchPoint(ts);
	
			if (2 <= ts.length) {
				const dist = touchDistance(ts);
				if (baseDist) {
					const s = dist / (baseDist * inst.scale);
					if (s && s !== Infinity) {
						_setScaledSize(inst, inst.scale * s);
					}
					frm.scrollLeft = ix * inst.scale - cx;
					frm.scrollTop  = iy * inst.scale - cy;
				}
				baseDist = dist / inst.scale;
			} else {
				frm.scrollLeft = ix * inst.scale - cx;
				frm.scrollTop  = iy * inst.scale - cy;
			}
		}, { passive: false });
	
		function touchPointToImagePoint(x, y) {
			const fsx = frm.scrollLeft;
			const fsy = frm.scrollTop;
			const ix = (x + fsx) / inst.scale;
			const iy = (y + fsy) / inst.scale;
			return [ix, iy];
		}
	
		function updatePoint(ts) {
			lastTouchCount = ts.length;
			[ix, iy] = touchPointToImagePoint(...getTouchPoint(ts));
		}
	}
	
	NS.applyImage = apply;

	/**
	 *
	 * Dialog
	 *
	 * @author Takuto Yanagida
	 * @version 2022-08-17
	 *
	 */
	
	
	function initialize(fs, opts = {}) {
		if (fs.length === 0) return;
	
		opts = Object.assign({
			styleRoot   : ':ncViewer',
			styleCloser : ':ncViewerCloser',
			stylePrev   : ':ncViewerPrev',
			styleNext   : ':ncViewerNext',
			styleCaption: ':ncViewerCaption',
	
			styleOpen     : ':ncOpen',
			styleVisible  : ':ncVisible',
			styleInstantly: ':ncInstantly',
	
			hashPrefix: 'viewer:',
		}, opts);
	
		const is = new Map();
		let prev = null;
		for (const f of fs) {
			const inst = createViewer(opts, f.id, f.frameInst, f.frame, f.caption, prev);
			f.opener.addEventListener('click', e => _onOpen(inst, e));
			is.set(f.id, inst);
			prev = inst;
		}
		assignEventHandler(is);
		processHash(is, opts['hashPrefix']);
	}
	
	function assignEventHandler(is) {
		onResize(() => {
			for (const inst of is.values()) inst.fi.doResize();
			setTimeout(() => {
				for (const inst of is.values()) inst.fi.doResize();
			}, 200);
		});
		window.addEventListener('keydown', e => {
			if (currentId !== null && !e.altKey && !e.ctrlKey && !e.shiftKey) {
				const cur = is.get(currentId);
				if (e.key === 'Escape') cur.dlg.click();
				else if (e.key === 'ArrowLeft') cur.btnPrev.click();
				else if (e.key === 'ArrowRight') cur.btnNext.click();
			}
		});
	}
	
	function processHash(is, hashPrefix) {
		window.addEventListener('popstate', e => {
			if (currentId !== null && is.has(currentId)) {
				doClose(is.get(currentId));
			}
			if (e.state && e.state['name'] === 'nc-viewer' && e.state['id'] !== undefined && is.has(e.state['id'])) {
				doOpen(is.get(e.state['id']));
			}
		});
		window.addEventListener('hashchange', () => checkHash(is, hashPrefix, location.hash));
		checkHash(is, hashPrefix, location.hash);
	}
	
	function checkHash(is, hashPrefix, hash) {
		if (location.hash.indexOf('#' + hashPrefix) !== 0) return;
		const h = hash.replace('#' + hashPrefix, '');
		if (!h) return;
		for (const [id, inst] of is) {
			if (id === h) {
				if (currentId !== null && currentId !== id) doClose(is.get(currentId));
				if (currentId === null || currentId !== id) doOpen(inst);
				break;
			}
		}
	}
	
	
	// -----------------------------------------------------------------------------
	
	
	let currentId = null;
	
	function createViewer(opts, id, frameInst, frame, caption, prevInstance = null) {
		const inst = {};
		inst.opts = opts;
		inst.fi   = frameInst;
		inst.id   = id;
	
		inst.dlg = document.createElement('div');
		setClass(inst.dlg, opts.styleRoot);
		inst.dlg.addEventListener('click', e => _onClose(e));
		inst.dlg.appendChild(frame);
	
		const btn = document.createElement('button');
		setClass(btn, opts.styleCloser);
		inst.dlg.appendChild(btn);
	
		inst.btnPrev = document.createElement('button');
		inst.btnNext = document.createElement('button');
		setClass(inst.btnPrev, opts.stylePrev);
		setClass(inst.btnNext, opts.styleNext);
		inst.dlg.appendChild(inst.btnPrev);
		inst.dlg.appendChild(inst.btnNext);
	
		if (caption) {
			const divCap = document.createElement('div');
			divCap.innerHTML = `<span>${caption}</span>`;
			setClass(divCap, opts.styleCaption);
			inst.dlg.appendChild(divCap);
		}
		document.body.appendChild(inst.dlg);
	
		_setAdjacentInstance(inst, prevInstance);
		prevInstance = inst;
		return inst;
	}
	
	
	// -----------------------------------------------------------------------------
	
	
	function doOpen(inst, instantly = false) {
		setClass(inst.dlg, inst.opts.styleOpen);
		inst.fi.doOpen();
	
		if (instantly) {
			inst.fi.doResize();
			setClass(inst.dlg, inst.opts.styleInstantly);
			setClass(inst.dlg, inst.opts.styleVisible);
			setTimeout(() => { setClass(inst.dlg, inst.opts.styleInstantly, false); }, 20);
	
			const url = '#' + inst.opts.hashPrefix + inst.id;
			history.replaceState(null, '', url);
		} else {
			setTimeout(() => {
				inst.fi.doResize();
				setClass(inst.dlg, inst.opts.styleVisible);
			}, 0);
		}
		currentId = inst.id;
	
		const sty = getComputedStyle(document.documentElement);
		if (sty.marginTop) {
			inst.dlg.style.top = sty.marginTop;
		}
	}
	
	function doClose(inst, instantly = false) {
		inst.fi.doClose();
	
		if (instantly) {
			setClass(inst.dlg, inst.opts.styleInstantly);
			setClass(inst.dlg, inst.opts.styleVisible, false);
			setTimeout(() => { setClass(inst.dlg, inst.opts.styleInstantly, false); }, 20);
		} else {
			setClass(inst.dlg, inst.opts.styleVisible, false);
		}
		setTimeout(() => { setClass(inst.dlg, inst.opts.styleOpen, false); }, 200);
		currentId = null;
	}
	
	
	// -----------------------------------------------------------------------------
	
	
	function _setAdjacentInstance(inst, prev) {
		inst.btnNext.style.display = 'none';
		if (prev) {
			inst.btnPrev.addEventListener('click', e => {
				e.stopPropagation();
				doClose(inst, true);
				doOpen(prev, true);
			});
			_setNextInstance(prev, inst);
		} else {
			inst.btnPrev.style.display = 'none';
		}
	}
	
	function _setNextInstance(inst, next) {
		inst.btnNext.addEventListener('click', e => {
			e.stopPropagation();
			doClose(inst, true);
			doOpen(next, true);
		});
		inst.btnNext.style.display = null;
	}
	
	function _onOpen(inst, e) {
		e.preventDefault();
		doOpen(inst);
	
		if (location.hash) {
			const newUrl = location.href.substring(0, location.href.indexOf('#'));
			history.replaceState(null, '', newUrl);
		}
		const url = '#' + inst.opts.hashPrefix + inst.id;
		history.pushState({ name: 'nc-viewer', id: inst.id }, null, url);
	}
	
	function _onClose(e) {
		e.preventDefault();
		history.back();
	}
	

})(window['NACSS']['viewer']);
